

.App {
    color: white;
    text-align: center;
    transition: 0.5s;
    font-size: 15px; 
    margin-bottom: 40px;
    font-family: Arial, Helvetica, sans-serif;
  } 
  
  .h5 {
    font-family: Arial, Helvetica, sans-serif;
  } 
  .searchinput{
    margin-top: 15px;
    margin-bottom: 10px;
    border-radius: 5px;
  }
 
  .button {
    display: inline-block;
    border-radius: 4px;
    background-color: #369497;
    border: none;
    color: white;
    text-align: center;
    font-size: 18px;
    padding: 12px;
    width: 120px;
    transition: all 0.5s;
    cursor: pointer;
    margin: 10px;
  }
  
  .button-pos {
    margin: 0;
    position: absolute;
    bottom: 0%;
    right: 0%;
    transform: translate(-20%, -15%);
  }
  
  .button span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
  }
  
  .button span:after {
    content: '\00bb';
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }
  
  .button:hover span {
    padding-right: 25px;
  }
  
  .button:hover span:after {
    opacity: 1;
    right: 0;
  }
  
  input[type=text] {
    width: 100%;
    box-sizing: border-box;
    border: 1px solid #62d5a3;
    border-radius: 6px;
    font-size: 16px; 
        color: white;  
    background-color: #000;
    padding: 12px 20px 12px 10px;
  }
  
  .searchbar-pos {
    margin: 0;
    position: absolute;
    top: 25%;
    left: 25%;
  }
  